@import url('https://fonts.googleapis.com/css2?family=Protest+Guerrilla&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&display=swap');

#aboutme{
    height: 100vh;
    position: relative; 
    
    width:100vw;
    /* outline: solid #333 2px; */
}
.aboutMePage{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    position: relative;
}
.aboutMe_box{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width:100vw;
    /* outline: solid #333 1px; */
}
#profileback{
    position:absolute;
    width: 30vw;
    height: 100vh;
    left: 0;
    background-color: white;
}
.profile{
   
}
#profileBox{
    padding: 1.5vw;
    position: relative;
    border-radius: 20px;
    background-color: #bbdcfb73;
    box-shadow: 0 0 20px #bbdcfb73;

}
#profileBox>h1{
    margin: 10px 0 20px 0;
    font-size: 3vw;
    position: absolute;
    top: -6vh;
    /* font-weight: 500; */
    letter-spacing: 1px;
    font-family: "Protest Guerrilla", sans-serif;
    color: #82AFE0;
    
}

.profileImg{
    border-radius: 5px;
    /* width: 250px; */
    width: 35vw;
}

#profileTable{
    margin-left: 5vw;
    width: 35vw;
    padding: 10px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
   
}
#profileTable>table>tr>td{
    /* border-bottom: solid burlywood 1px; */
    
}

#profileTable>table>tr>td>span{
    font-size: 1vw;
}
#profileTable>table>tr>td:nth-child(1){
    font-size: 1.4vw;
    font-weight: 550;
    padding-right: 1.5vw;
}
#profileTable>table>tr>td{
    padding: 5px;
    font-size: 1.2vw;
}

